* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited,
a:hover {
  text-decoration: none;
}
ul,
li {
  list-style: none;
}
img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 auto !important;
  font-size: inherit;
  font-weight: 400;
}

body {
  padding: 50px;
  background: linear-gradient(to right, rgb(217, 160, 255), rgb(97, 218, 255));
}

.section {
  padding: 15px;
  margin: 0 auto;
  width: 600px;
}

.title {
  padding: 10px 15px;

  font-size: 30px;
  text-transform: uppercase;
  font-weight: 500;
  color: rgb(63, 63, 63);

  border: 2px solid rgba(2, 2, 2, 0.356);
}
